"use client";
import { useEffect, useMemo, useState, useRef } from "react";
import type { AuthUser } from "@/lib/auth";

type Props = { initialUser: AuthUser };

type ProfileData = {
  name: string;
  email: string;
  phone: string;
  jobTitle: string;
  company: string;
  account: {
    twoFactor: boolean;
    newsletter: boolean;
  };
  avatarUrl?: string;
};

function sanitize(input: string) {
  // Minimal XSS guard: strip angle brackets and control chars; React escapes output too
  return input.replace(/[<>\u0000-\u001F\u007F]/g, "");
}

export default function ProfileClient({ initialUser }: Props) {
  const storageKey = useMemo(() => `profile:draft:${initialUser.id}`, [initialUser.id]);
  const [saving, setSaving] = useState(false);
  const [savedMsg, setSavedMsg] = useState<string | null>(null);
  const [errorMsg, setErrorMsg] = useState<string | null>(null);
  const [mounted, setMounted] = useState(false);
  const [baseline, setBaseline] = useState<ProfileData | null>(null);
  const [uploading, setUploading] = useState(false);
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const [form, setForm] = useState<ProfileData>({
    name: initialUser.name ?? "",
    email: initialUser.email,
    phone: "",
    jobTitle: "",
    company: "",
    account: { twoFactor: false, newsletter: false },
    avatarUrl: (initialUser as any).avatarUrl ?? "",
  });

  useEffect(() => { setMounted(true); }, []);

  // Load from API and restore draft from sessionStorage
  useEffect(() => {
    let cancelled = false;
    (async () => {
      try {
        const res = await fetch("/api/profile", { cache: "no-store" });
        if (!res.ok) throw new Error("Falha ao buscar perfil");
        const data = (await res.json()) as ProfileData;
        if (!cancelled) {
          const draft = sessionStorage.getItem(storageKey);
          const value = draft ? { ...data, ...JSON.parse(draft) } : data;
          setForm(value);
          setBaseline(value);
        }
      } catch (e) {
        // Fallback to initial user
        const draft = sessionStorage.getItem(storageKey);
        if (!cancelled) {
          setForm((prev) => draft ? { ...prev, ...JSON.parse(draft) } : prev);
          setBaseline((prev) => prev ?? null);
        }
      }
    })();
    return () => { cancelled = true; };
  }, [storageKey]);

  // Persist draft within session (maintain state during navigation)
  useEffect(() => {
    sessionStorage.setItem(storageKey, JSON.stringify(form));
  }, [form, storageKey]);

  function setField<K extends keyof ProfileData>(key: K, value: ProfileData[K]) {
    setForm((f) => ({ ...f, [key]: value }));
  }

  function onInput(e: React.ChangeEvent<HTMLInputElement>) {
    const { name, value, type, checked } = e.target;
    if (name === "twoFactor" || name === "newsletter") {
      setForm((f) => ({ ...f, account: { ...f.account, [name]: checked } }));
    } else {
      setForm((f) => ({ ...f, [name]: sanitize(value) } as ProfileData));
    }
  }

  function onCancel() {
    if (baseline) {
      setForm(baseline);
      sessionStorage.setItem(storageKey, JSON.stringify(baseline));
      setSavedMsg("Edição cancelada");
      setTimeout(() => setSavedMsg(null), 1200);
    }
  }

  async function onUploadAvatar(file: File) {
    if (!file) return;
    if (!["image/png", "image/jpeg", "image/webp"].includes(file.type)) {
      setErrorMsg("Imagem deve ser PNG, JPG ou WEBP");
      return;
    }
    if (file.size > 2 * 1024 * 1024) {
      setErrorMsg("Imagem excede 2MB");
      return;
    }
    setErrorMsg(null);
    setUploading(true);
    try {
      const fd = new FormData();
      fd.append("file", file);
      const res = await fetch("/api/profile/avatar", { method: "POST", body: fd });
      if (!res.ok) throw new Error(await res.text());
      const data = await res.json();
      setForm((f) => ({ ...f, avatarUrl: data.avatarUrl }));
      setBaseline((b) => (b ? { ...b, avatarUrl: data.avatarUrl } : b));
      setSavedMsg("Foto atualizada");
      setTimeout(() => setSavedMsg(null), 1200);
    } catch (err: any) {
      setErrorMsg(typeof err?.message === "string" ? err.message : "Falha ao enviar imagem");
    } finally {
      setUploading(false);
      if (fileInputRef.current) fileInputRef.current.value = "";
    }
  }

  function validate(): string[] {
    const errs: string[] = [];
    if (!form.name.trim()) errs.push("Nome é obrigatório");
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) errs.push("E-mail inválido");
    if (form.phone && !/^\+?[0-9\s().-]{7,}$/.test(form.phone)) errs.push("Telefone inválido");
    if (form.jobTitle.length > 80) errs.push("Cargo muito longo");
    if (form.company.length > 120) errs.push("Empresa muito longa");
    return errs;
  }

  async function onSave(e: React.FormEvent) {
    e.preventDefault();
    setSavedMsg(null);
    setErrorMsg(null);
    const errs = validate();
    if (errs.length) {
      setErrorMsg(errs.join("; "));
      return;
    }
    setSaving(true);
    try {
      const res = await fetch("/api/profile", {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      if (!res.ok) throw new Error(await res.text());
      const data = (await res.json()) as ProfileData;
      setForm(data);
      setBaseline(data);
      setSavedMsg("Alterações salvas");
      setTimeout(() => setSavedMsg(null), 1500);
    } catch (err: any) {
      setErrorMsg(typeof err?.message === "string" ? err.message : "Falha ao salvar");
    } finally {
      setSaving(false);
    }
  }

  return (
    <main className="profile-page">
      <h1 className="profile-title">Meu Perfil</h1>

      {/* Avatar + Upload */}
      <section className="profile-avatar">
        <div className="avatar-preview" aria-busy={uploading}>
          {form.avatarUrl ? (
            // eslint-disable-next-line @next/next/no-img-element
            <img src={form.avatarUrl} alt="Foto de perfil" />
          ) : (
            <div className="avatar-fallback">{form.name?.[0] || form.email?.[0]}</div>
          )}
        </div>
        <div className="avatar-actions">
          <label className="btn-secondary">
            Alterar foto
            <input
              ref={fileInputRef}
              type="file"
              accept="image/png,image/jpeg,image/webp"
              style={{ display: "none" }}
              onChange={(e) => {
                const file = e.target.files?.[0];
                if (file) onUploadAvatar(file);
              }}
            />
          </label>
        </div>
      </section>

      <form className="profile-form" onSubmit={onSave} noValidate>
        {/* Informações pessoais */}
        <fieldset>
          <legend>Informações pessoais</legend>
          <label>
            Nome
            <input name="name" value={form.name} onChange={onInput} required maxLength={100} />
          </label>
          <label>
            E-mail
            <input name="email" type="email" value={form.email} onChange={onInput} required />
          </label>
          <label>
            Telefone
            <input name="phone" type="tel" value={form.phone} onChange={onInput} placeholder="+55 11 99999-9999" />
          </label>
        </fieldset>

        {/* Dados profissionais */}
        <fieldset>
          <legend>Dados profissionais</legend>
          <label>
            Cargo
            <input name="jobTitle" value={form.jobTitle} onChange={onInput} />
          </label>
          <label>
            Empresa
            <input name="company" value={form.company} onChange={onInput} />
          </label>
        </fieldset>

        {/* Configurações de conta */}
        <fieldset>
          <legend>Configurações de conta</legend>
          <label className="inline">
            <input name="twoFactor" type="checkbox" checked={form.account.twoFactor} onChange={onInput} />
            Ativar 2FA
          </label>
          <label className="inline">
            <input name="newsletter" type="checkbox" checked={form.account.newsletter} onChange={onInput} />
            Receber novidades
          </label>
        </fieldset>

        <div className="actions">
          <button className="save-btn" type="submit" disabled={saving} aria-busy={saving}>
            {saving ? "Salvando..." : "Salvar"}
          </button>
          <button className="btn-secondary" type="button" onClick={onCancel} disabled={saving}>
            Cancelar
          </button>
          <a className="btn-tertiary" href="/profile/password">Alterar senha</a>
          {savedMsg && <span className="ok">{savedMsg}</span>}
          {errorMsg && <span className="err" role="alert">{errorMsg}</span>}
        </div>
      </form>
    </main>
  );
}